import React, {Component} from 'react';

/**
 * 
 */
class LoginDiv extends Component{
    render(){
        return (
            <div className="loginDiv">
                <div>
                    <ul>
                        {this.props.data.map(
                            function(item,i){
                                if(i == 0 || i == 1 || i == 3 || i == 5){
                                    return <li className="menu" key={i}>{item}</li>
                                }else{
                                    return  <li key={i}>{item}</li>
                                }
                            },this
                        )}
                    </ul>
                </div>
            </div>
        )
    }
}

/**
 * 
 */
class SearchDiv extends Component{
    render(){
        return (
            <div className="searchDiv">
                <div>
                    <div></div>
                    <div className="searchInput">
                        <div className="searchType">
                            <ul>
                                <li className="active"></li>
                                <li></li>
                            </ul>
                        </div>
                        <div className="searchBorder">
                             <span className="inputDiv">
                                 <input type="text" defaultValue="JXYFOREVER"/>
                             </span>
                            <span className="searchButton"></span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

/**
 * 
 */
class NavDiv extends Component{
    /**
     * propsstate
     */
    constructor(props){
        super(props);
        this.state = {
            index: 0,
        };
    }

    /**
     * -->state-->
     */
    handleMenuClick(i){
        this.setState({
            index:i
        });
    }
    render(){
        return (
            <div className="navDiv">
                <div>
                    <ul>
                        {this.props.data.map(
                            function(item,i){
                                return <li onClick={this.handleMenuClick.bind(this,i)} className={this.state.index == i ? 'active' : ''} key={i}>{item.name}</li>
                            },this
                        )}
                    </ul>
                </div>
            </div>
        )
    }
}

/***
 * 
 */
class Top extends Component{

    /**
     * 
     */
    render() {
        return (
            <div className="top">
                <LoginDiv data={this.props.loginDiv}/>
                <SearchDiv />
                <NavDiv data={this.props.nav}/>
            </div>
            );
    }

    componentWillMount() {
        console.log('Component WILL MOUNT!')
    }
    componentDidMount() {
        console.log('Component DID MOUNT!')
    }
    componentWillReceiveProps(newProps) {
        console.log('Component WILL RECEIVE PROPS!')
    }
    shouldComponentUpdate(newProps, newState) {
        return true;
    }
    componentWillUpdate(nextProps, nextState) {
        console.log('Component WILL UPDATE!');
    }
    componentDidUpdate(prevProps, prevState) {
        console.log('Component DID UPDATE!')
    }
    componentWillUnmount() {
        console.log('Component WILL UNMOUNT!')
    }
}

/**
 * 
 * @type {{loginDiv: string[], nav: string[]}}
 */
Top.defaultProps = {
    loginDiv:["","","|","","|","","|"],
    nav:[{
        "name":"",
        "select":true,
    },{
        "name":"",
        "select":false,
    },{
        "name":"",
        "select":false,
    },{
        "name":"",
        "select":false,
    },{
        "name":"",
        "select":false,
    }]
}

export default Top


